.div1 {
  width: 2307px;
  height: 667px;
}
.div1 .titleNode {
  width: 384px;
  height: 120px;
  /* float: left; */
  line-height: 103px;
  text-align: center;
  cursor: pointer;
  position: relative;
  /* margin-left: 88px; */
  font-size: 44px;
  text-shadow: 0 0 30px #f4faf5;
  color: #fff;
}
.div1 .realtime {
  width: 2307px;
  height: 535px;
  border-style: solid;
  border-color: transparent;
  border-width: 400px 310px 60px 165px;
  border-image: url(/images/imgs/框.png);
  border-image-slice: 400 310 60 165 fill;
  padding: 0;
}
.div1 .realtime .sbox {
  height: 528px;
  margin-top: -400px;
  margin-left: -140px;
  margin-right: -432px;
  margin-bottom: -15px;
}
.div1 .realtime .sbox .demo {
  overflow: hidden;
  height: 380px;
}
.div1 .realtime .sbox .demo2 {
  margin-top: -20px;
}
.div1 .realtime .sbox .smalltable {
  width: 2225px;
  height: 528px;
  overflow: hidden;
}
.div1 .realtime .sbox .smalltable .table {
  width: 100%;
  padding-top: 50px;
  padding-left: 60px;
  margin: 44px 0 0 46px;
}
.div1 .realtime .sbox .smalltable .table th {
  height: 90px;
  border-top: none;
  border-bottom: 0px solid red;
  font-weight: bold;
  line-height: 1;
  font-size: 40px;
  padding: 16px 8px;
  color: white;
  text-align: center;
}
.div1 .realtime .sbox .smalltable .table td {
  height: 81px;
  border-top: none;
  border-bottom: 0px solid red;
  line-height: 1;
  font-size: 40px;
  padding: 16px 8px;
  color: white;
  text-align: center;
}
.div1 .realtime .sbox .smalltable .table .striped1 {
  background-color: #172362;
}
.div1 .realtime .sbox .smalltable .table .striped1 tr {
  text-align: center;
}
.div1 .realtime .sbox .smalltable .table .striped {
  background-color: #0a5bb2;
}
.div1 .realtime .sbox .smalltable .table .striped2 {
  background-color: #133884;
}
